@import "vars";

.fancybox-bg { // fancybox overlay
    background: rgba(0,0,0,.8); }

.fancybox-content {
    overflow-x: hidden; }

.fancybox-slide--html .fancybox-close-small {
    color: #222; }

.fancybox-lock {
    overflow: hidden;
    margin-right: 0 !important; }

.fancybox-lock .fancybox-overlay {
    overflow: hidden;
    overflow-y: hidden; }

body.compensate-for-scrollbar {
    overflow-y: auto; }

.compensate-for-scrollbar {
    margin-right: 0 !important; }
